.BirthdayAndAnniversaries {

  .carousel-indicators {
    bottom: -31px;
      li {
        box-sizing: content-box;
        flex: 0 1 auto;
        width: 7px;
        height: 7px;
        border-radius: 0px;
        margin-right: 3px;
        margin-left: 3px;
        text-indent: -999px;
        cursor: pointer;
        background-color: #fff;
        background-clip: padding-box;
        border-top: 10px solid transparent;
        border-bottom: 10px solid transparent;
        opacity: .5;
        transition: opacity 0.6s ease;
    }
  }

  .title {
    font-size: 1.5rem;
    font-weight: 400;
    padding: 1rem;
  }

    .card {
        font-family: "Segoe UI";
        background-color: #fff;
        border-radius: 0;
        height: 254px;
        margin-top: 50px;
        margin-bottom: 50px;
        box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.2);
        overflow: hidden;
        }
    .card-image {
        max-width: 40%;
        overflow: hidden;
        padding: 0;      
        align: center;  
        vertical-align: middle;
        float: left;
      }
      
      .card-image img {
        /* max-width: 100%; */
        max-height: 254px;
        align: center;
        vertical-align: middle;
        float: left;
      }
      .card-body {
        display: block;
      }
      .avatar {
        img {
                  width: 100px;
          height: 100px;
          border-radius: 50%;
    
          top: 30px;
          left: 20px;
        }
    }
    .carousel-indicators li {
        background-color: darkgray; //this can be whatever color you want
      }
      
      
}

